<template>
  <section class="dashboard-coin-container">
    <div
      v-for="item in coins"
      :key="item.key"
      class="dashboard-coin-container__item">
      <div class="dashboard-coin-container__item__main">
        <div class="dashboard-coin-container__block">
          <Icon class="dashboard-coin-container__icon" :name="item.icon  + '-circle'"/>
          <div class="dashboard-coin-container__info-per-coin">
            <p class="dashboard-coin-container__count">{{ ` ${item.coinCount} ${item.title}`}}</p>
            <p class="dashboard-coin-container__price-by-one">${{ item.coinPrice}}</p>
          </div>
        </div>
        <p
          class="dashboard-coin-container__difference"
          :class="{
          'dashboard-coin-container__difference-plus': difference(item) > 0,
          'dashboard-coin-container__difference-minus': difference(item) < 0
          }">
          {{ showDifference(difference(item)) }}</p>
      </div>
      <div class="dashboard-coin-container__item__divider"></div>
      <div class="dashboard-coin-container__item__info">
        <div class="dashboard-coin-container__item__accrued">
          <p class="dashboard-coin-container__item__accrued__title">{{ t('dashboardCoinContainer.accrued') }}</p>
          <p class="dashboard-coin-container__item__accrued__value">${{ item.accrualRate }}</p>
        </div>
        <div class="dashboard-coin-container__item__current">
          <p class="dashboard-coin-container__item__current__title">{{ t('dashboardCoinContainer.current') }}</p>
          <p class="dashboard-coin-container__item__current__value">${{ item.currentRate }}</p>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { computed } from 'vue'
import { useI18n } from 'vue-i18n'
import Icon from '@/components/Icon/Icon'
export default {
  name: 'DashboardCoinContainer',
  components: { Icon },
  setup () {
    const { t } = useI18n()

    const coins = computed(() => ([
      {
        title: 'BTC',
        subtitle: 'Litecoin',
        key: 'btc',
        icon: 'btc',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 46251.42,
        currentRate: 36251.42
      },
      {
        title: 'LTC',
        subtitle: 'Litecoin',
        key: 'ltc',
        icon: 'ltc',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 26251.42,
        currentRate: 34251.42
      },
      {
        title: 'ETH',
        subtitle: 'Litecoin',
        key: 'eth',
        icon: 'eth',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 32251.42,
        currentRate: 36251.42
      },
      {
        title: 'XMR',
        subtitle: 'Monero',
        key: 'xmr',
        icon: 'xmr',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 36251.42,
        currentRate: 31251.42
      },
      {
        title: 'USDT',
        subtitle: 'ERC-20',
        key: 'usdt',
        icon: 'usdt',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 41251.42,
        currentRate: 36251.42
      },
      {
        title: 'Perfect Money',
        subtitle: null,
        key: 'pm',
        icon: 'pm',
        coinCount: 91.24512,
        coinPrice: 50000.42,
        accrualRate: 31251.42,
        currentRate: 46251.42
      }
    ]))

    const difference = (item) => {
      return item.currentRate - item.accrualRate
    }

    const showDifference = (value) => {
      return (value >= 0) ? `+$${value}` : `-$${Math.abs(value)}`
    }

    return {
      t,
      coins,
      difference,
      showDifference
    }
  }
}
</script>

