<template>
  <section class="overview"
    v-click-outside="handleClickOutside"
  >
    <div class="overview__title">Overview</div>
    <div class="overview__tab">
      <div
        v-for="item in tabElements"
        :key="item.key"
        :class="{ 'overview__tab__item__active': activeTab === item.key }"
        class="overview__tab__item"
        @click="changeActiveTab(item.key)"
      >
        <Icon v-if="!(activeTab === item.key)" :name="item.icon"/>
        <Icon v-else :name="item.activeIcon"/>
        <p class="overview__tab__title">{{ item.title }}</p>

      </div>
    </div>

    <div v-show="activeTab === 'custom'"
      class="overview__calendar"
    >
      <div class="overview__calendar__input-group">
        <Input
          v-model="inputValue.start"
          class="overview__calendar__input"
          :placeholder="t('overview.from')"
          opacity
          v-mask.unmask.hide-on-empty="{mask: 'NN/NN/NNNN', model: 'inputValue.start'}"
          @blur="validateDate('start')"
        >
          <template #prepend>
            <Icon class="deposit-page__email-notify-bell" name="calendar"/>
          </template>
        </Input>
        <div class="overview__calendar__divider"></div>
        <Input
          v-model="inputValue.end"
          class="overview__calendar__input"
          :placeholder="t('overview.to')"
          opacity
          v-mask.unmask.hide-on-empty="{mask: 'NN/NN/NNNN', model: 'inputValue.end'}"
          @blur="validateDate('end')"
        >
          <template #prepend>
            <Icon class="deposit-page__email-notify-bell" name="calendar"/>
          </template>
        </Input>
      </div>
      <v-date-picker
        ref="datePicker"
        v-model="dateRange"
        :columns="2"
        color="orange"
        :first-day-of-week="1"
        :masks="{
          title: 'MMM YYYY',
          weekdays: 'WWW'
        }"
        :locale="locale"
        is-range
      >
      </v-date-picker>
    </div>
  </section>
</template>

<script>
import { computed, ref, watch, reactive } from 'vue'
import { useI18n } from 'vue-i18n'
import { VMaskDirective } from 'v-slim-mask'
import clickOutside from '@/directives/clickOutside'
import moment from 'moment'
import Input from '@/components/UIKit/Input'
import Icon from '@/components/Icon/Icon'

export default {
  name: 'Overview',
  components: { Icon, Input },
  directives: {
    mask: VMaskDirective,
    clickOutside
  },
  setup () {
    const { t, locale } = useI18n()
    const datePicker = ref(null)
    const activeTab = ref('today')
    const dateRange = ref(null)
    const inputValue = reactive({ start: '', end: '' })

    const changeActiveTab = tab => {
      activeTab.value = tab
    }

    watch(dateRange, data => {
      if (data.start) {
        data.start = moment(data.start).format('DD/MM/YYYY')
        data.end = moment(data.end).format('DD/MM/YYYY')
      }
      inputValue.start = data.start
      inputValue.end = data.end
    })

    const handleClickOutside = () => {
      activeTab.value = ''
    }
    const tabElements = computed(() => ([
      { title: t('overview.today'), icon: 'today', activeIcon: 'todayActive', key: 'today' },
      { title: t('overview.week'), icon: 'week', activeIcon: 'weekActive', key: 'week' },
      { title: t('overview.month'), icon: 'month', activeIcon: 'monthActive', key: 'month' },
      { title: t('overview.lastMonth'), icon: 'lastMonth', activeIcon: 'lastMonthActive', key: 'lastMonth' },
      { title: t('overview.custom'), icon: 'custom', activeIcon: 'customActive', key: 'custom' },
      { title: t('overview.allTime'), icon: 'allTime', activeIcon: 'allTimeActive', key: 'allTime' }
    ]))

    const parseDate = date => {
      return date.split('/').reverse().join('-').replace(/\s/g, '')
    }

    const validateDate = async dateType => {
      const parsedValue = parseDate(inputValue[dateType])
      const isValid = moment(parsedValue).isValid()
      if (isValid) {
        await datePicker.value.updateValue({
          start: parseDate(inputValue.start),
          end: parseDate(inputValue[dateType])
        })
        await datePicker.value.adjustPageRange(true)
        if (dateType === 'start') {
          inputValue.end = ''
        }
      }
    }

    return {
      t,
      tabElements,
      activeTab,
      dateRange,
      locale,
      inputValue,
      datePicker,
      validateDate,
      handleClickOutside,
      changeActiveTab

    }
  }
}
</script>

