<template>
  <canvas ref="chartLine" :width="width" :height="height"></canvas>
</template>

<script>
import Chart from 'chart.js'
import { useI18n } from 'vue-i18n'
import { ref, onMounted } from 'vue'

export default {
  name: 'CreateLineGraphs',
  props: {
    width: {
      type: Number,
      require: true
    },
    height: {
      type: Number,
      require: true
    },
    type: {
      type: String,
      require: true
    },
    data: {
      type: Object,
      require: true
    },
    options: {
      type: Object,
      require: true
    }
  },
  setup (props, { emit }) {
    const { t } = useI18n()
    const chartLine = ref(null)
    let myChart = ref(null)
    onMounted(() => {
      const ctx = chartLine.value.getContext('2d')
      // 'linear-gradient(90deg, #6BBCCF 0.11%, #5279C9 100%)'
      const gradientFill = ctx.createLinearGradient(0, 100, 0, 0)
      gradientFill.addColorStop(0, 'rgba(107, 188, 207, 0.11)')
      gradientFill.addColorStop(1, 'rgba(82, 121, 201, 1)')

      myChart = new Chart(ctx, {
        type: 'line',
        data: {
          labels: [
            t('graphs.mon'),
            t('graphs.tue'),
            t('graphs.wed'),
            t('graphs.thu'),
            t('graphs.fri'),
            t('graphs.sat'),
            t('graphs.sun')
          ],
          datasets: [{
            label: '# of Votes',
            data: [1112, 1623, 2357, 2058, 2156, 724, 598],
            backgroundColor: gradientFill,
            borderColor: '#6CA7D5',
            borderWidth: 1
          }]
        },
        options: {
          tooltips: {
            cornerRadius: 0,
            caretSize: 0,
            xPadding: 12,
            yPadding: 8,
            backgroundColor: '#11193D',
            titleTextColor: '#FAFAFA',
            titleFontStyle: 'normal',
            titleFontWidth: 'normal',
            titleMarginBottom: 15,
            titleFontSize: 18,
            callbacks: {
              title: function (tooltipItem, data) {
                const title = `$${tooltipItem[0].value}`
                return title
              },
              label: function (tooltipItem, data) {
                const lable = '+5% then last week'
                return lable
              }
            }
          },
          legend: {
            display: false
          },
          scales: {
            xAxes: [{
              ticks: {
                // fontSize: 12,
                // lineHeight: 14,
                fontColor: 'rgba(0,0,0,0.5)',
                fontStyle: 'bold',
                maxTicksLimit: 5,
                padding: 15
              },
              gridLines: {
                zeroLineColor: 'transparent',
                drawTicks: false,
                display: false

              }

            }],
            yAxes: [{
              gridLines: {
                color: 'rgba(14,41,108,0.1)',
                zeroLineColor: '#E6E8ED',
                borderDash: [10, 10]
              },
              ticks: {
                padding: 7,
                beginAtZero: true,
                fontColor: 'rgba(0,0,0,0.5)',
                fontStyle: 'bold',
                callback: function (value, index, values) {
                  const valueWithFormat = new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(value)
                  return valueWithFormat
                }
              }
            }]
          }
        }
      })
    })
    /*
Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(context.parsed.y)
* */
    return {
      chartLine,
      myChart
    }
  }
}
</script>
