<template>
  <canvas ref="chart" :width="width" :height="height"></canvas>
</template>

<script>
import Chart from 'chart.js'
import { ref, onMounted } from 'vue'

export default {
  name: 'CreateGraphs',
  emits: ['ctxElement'],
  props: {
    width: {
      type: Number,
      require: true
    },
    height: {
      type: Number,
      require: true
    },
    type: {
      type: String,
      require: true
    },
    data: {
      type: Object,
      require: true
    },
    options: {
      type: Object,
      require: true
    }
  },
  setup (props, { emit }) {
    const chart = ref(null)
    let myChart = ref(null)
    onMounted(() => {
      const ctx = chart.value.getContext('2d')
      emit('ctxElement', ctx)
      myChart = new Chart(ctx, {
        type: props.type,
        data: props.data,
        options: props.options
      })
    })

    return {
      chart,
      myChart
    }
  }
}
</script>
